"use client"

import { useState } from "react"
import { Plus, Edit2, Trash2, X, FolderTree } from "lucide-react"
import { mockCategories } from "@/lib/mock-data"

type Category = (typeof mockCategories)[0]

export default function AdminCategorias() {
  const [categories, setCategories] = useState(mockCategories)
  const [showModal, setShowModal] = useState(false)
  const [editingCategory, setEditingCategory] = useState<Category | null>(null)
  const [formData, setFormData] = useState({
    nome: "",
    slug: "",
    ativo: true,
  })

  const openCreateModal = () => {
    setEditingCategory(null)
    setFormData({ nome: "", slug: "", ativo: true })
    setShowModal(true)
  }

  const openEditModal = (category: Category) => {
    setEditingCategory(category)
    setFormData({
      nome: category.nome,
      slug: category.slug,
      ativo: category.ativo,
    })
    setShowModal(true)
  }

  const generateSlug = (nome: string) => {
    return nome
      .toLowerCase()
      .normalize("NFD")
      .replace(/[\u0300-\u036f]/g, "")
      .replace(/[^a-z0-9]+/g, "-")
      .replace(/(^-|-$)/g, "")
  }

  const handleNomeChange = (nome: string) => {
    setFormData({
      ...formData,
      nome,
      slug: editingCategory ? formData.slug : generateSlug(nome),
    })
  }

  const handleSave = () => {
    if (editingCategory) {
      setCategories(
        categories.map((c) =>
          c.id === editingCategory.id ? { ...c, nome: formData.nome, slug: formData.slug, ativo: formData.ativo } : c,
        ),
      )
    } else {
      const newCategory: Category = {
        id: (categories.length + 1).toString(),
        nome: formData.nome,
        slug: formData.slug,
        produtosCount: 0,
        ativo: formData.ativo,
      }
      setCategories([...categories, newCategory])
    }
    setShowModal(false)
  }

  const handleDelete = (id: string) => {
    if (confirm("Tem certeza que deseja excluir esta categoria?")) {
      setCategories(categories.filter((c) => c.id !== id))
    }
  }

  const toggleStatus = (id: string) => {
    setCategories(categories.map((c) => (c.id === id ? { ...c, ativo: !c.ativo } : c)))
  }

  return (
    <div className="p-8">
      {/* Header */}
      <div className="flex items-center justify-between mb-8">
        <div>
          <h1 className="text-3xl font-light text-foreground">Categorias</h1>
          <p className="text-foreground/60">Organize seus produtos em categorias</p>
        </div>
        <button
          onClick={openCreateModal}
          className="flex items-center gap-2 bg-primary text-primary-foreground px-4 py-2 rounded-lg font-medium hover:bg-primary/90 transition-colors"
        >
          <Plus size={18} />
          Nova Categoria
        </button>
      </div>

      {/* Categories Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {categories.map((category) => (
          <div
            key={category.id}
            className={`bg-card border rounded-xl p-6 transition-all ${
              category.ativo ? "border-border" : "border-border/50 opacity-60"
            }`}
          >
            <div className="flex items-start justify-between mb-4">
              <div className="p-3 rounded-lg bg-muted">
                <FolderTree size={24} className="text-accent" />
              </div>
              <div className="flex items-center gap-1">
                <button
                  onClick={() => openEditModal(category)}
                  className="p-2 text-foreground/60 hover:text-primary hover:bg-muted rounded-lg transition-colors"
                >
                  <Edit2 size={16} />
                </button>
                <button
                  onClick={() => handleDelete(category.id)}
                  className="p-2 text-foreground/60 hover:text-red-600 hover:bg-red-50 rounded-lg transition-colors"
                >
                  <Trash2 size={16} />
                </button>
              </div>
            </div>

            <h3 className="font-medium text-foreground mb-1">{category.nome}</h3>
            <p className="text-sm text-foreground/60 mb-4">/{category.slug}</p>

            <div className="flex items-center justify-between">
              <span className="text-sm text-foreground/60">{category.produtosCount} produtos</span>
              <button
                onClick={() => toggleStatus(category.id)}
                className={`px-3 py-1 rounded-full text-xs font-medium transition-colors ${
                  category.ativo
                    ? "bg-green-100 text-green-700 hover:bg-green-200"
                    : "bg-gray-100 text-gray-700 hover:bg-gray-200"
                }`}
              >
                {category.ativo ? "Ativo" : "Inativo"}
              </button>
            </div>
          </div>
        ))}
      </div>

      {/* Modal */}
      {showModal && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
          <div className="bg-card border border-border rounded-xl w-full max-w-md">
            <div className="flex items-center justify-between p-6 border-b border-border">
              <h2 className="text-xl font-medium text-foreground">
                {editingCategory ? "Editar Categoria" : "Nova Categoria"}
              </h2>
              <button onClick={() => setShowModal(false)} className="text-foreground/60 hover:text-foreground">
                <X size={20} />
              </button>
            </div>

            <div className="p-6 space-y-4">
              <div>
                <label className="block text-sm font-medium text-foreground mb-2">Nome da Categoria</label>
                <input
                  type="text"
                  value={formData.nome}
                  onChange={(e) => handleNomeChange(e.target.value)}
                  className="w-full px-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
                  placeholder="Ex: Papelaria Personalizada"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-foreground mb-2">Slug (URL)</label>
                <input
                  type="text"
                  value={formData.slug}
                  onChange={(e) => setFormData({ ...formData, slug: e.target.value })}
                  className="w-full px-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
                  placeholder="papelaria-personalizada"
                />
              </div>

              <div className="flex items-center gap-3">
                <input
                  type="checkbox"
                  id="ativo"
                  checked={formData.ativo}
                  onChange={(e) => setFormData({ ...formData, ativo: e.target.checked })}
                  className="accent-accent"
                />
                <label htmlFor="ativo" className="text-sm text-foreground">
                  Categoria ativa
                </label>
              </div>
            </div>

            <div className="flex items-center justify-end gap-3 p-6 border-t border-border">
              <button
                onClick={() => setShowModal(false)}
                className="px-4 py-2 border border-border rounded-lg hover:bg-muted transition-colors"
              >
                Cancelar
              </button>
              <button
                onClick={handleSave}
                className="px-4 py-2 bg-primary text-primary-foreground rounded-lg hover:bg-primary/90 transition-colors"
              >
                {editingCategory ? "Salvar" : "Criar"}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
